import React, { Component } from 'react'
import * as $ from 'jquery';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
class MontenegroComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
        statusMontenegro: [],
        loading: true
    }
    this.getStatusMontenegro = this.getStatusMontenegro.bind(this);
}

_isMounted = false;

componentDidMount() {
  this._isMounted = true;
  this.getStatusMontenegro();
}

getStatusMontenegro() {
  var url = `https://covid2019-api.herokuapp.com/v2/country/montenegro`;
  $.get(url, (data) => {
      //console.log(data);
      if (this._isMounted){
        this.setState({
          statusMontenegro: data,
          loading: false
        });
      }
  });
}

componentWillUnmount() {
  this._isMounted = false;
}

  render(){
    if (this.state.loading === true) {
    return (
        <TableRow className="wrapper">
            <TableCell className="loading" colSpan="7">Učitavam podatke...</TableCell>
        </TableRow>
    )
    } else {
      console.log(this.state)
      let montenegro = this.state.statusMontenegro;
      return (
          <TableRow>
              <TableCell align="right">4</TableCell>
              <TableCell align="right">Crna Gora</TableCell>
              <TableCell align="right">631,219</TableCell>
              <TableCell align="right">{montenegro.data.confirmed}</TableCell>
              <TableCell align="right">{montenegro.data.active}</TableCell>
              <TableCell align="right">{montenegro.data.recovered}</TableCell>
              <TableCell align="right">{montenegro.data.deaths}</TableCell>
          </TableRow>
      );
    }
  }
}
export default MontenegroComponent;