import React, { Component } from 'react'
import * as $ from 'jquery';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
class SloveniaComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
        statusSlovenia: [],
        loading: true
    }
    this.getStatusSlovenia = this.getStatusSlovenia.bind(this);
}

_isMounted = false;

componentDidMount() {
  this._isMounted = true;
  this.getStatusSlovenia();
}

getStatusSlovenia() {
  var url = `https://covid2019-api.herokuapp.com/v2/country/slovenia`;
  $.get(url, (data) => {
      //console.log(data);
      if (this._isMounted){
        this.setState({
          statusSlovenia: data,
          loading: false
        });
      }
    });
  }

  componentWillUnmount() {
    this._isMounted = false;
  }
  
  render(){
    if (this.state.loading === true) {
    return (
        <TableRow className="wrapper">
            <TableCell className="loading" colSpan="7">Učitavam podatke...</TableCell>
        </TableRow>
    )
    } else {
      console.log(this.state)
      let slovenia = this.state.statusSlovenia;
      return (
          <TableRow>
              <TableCell align="right">5</TableCell>
              <TableCell align="right">Slovenija</TableCell>
              <TableCell align="right">2,094,060</TableCell>
              <TableCell align="right">{slovenia.data.confirmed}</TableCell>
              <TableCell align="right">{slovenia.data.active}</TableCell>
              <TableCell align="right">{slovenia.data.recovered}</TableCell>
              <TableCell align="right">{slovenia.data.deaths}</TableCell>
          </TableRow>
      );
    }
  }
}
export default SloveniaComponent;