import React, { Component } from 'react'
import * as $ from 'jquery';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
class BosniaComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
        statusBosnia: [],
        loading: true
    }
    this.getStatusBosnia = this.getStatusBosnia.bind(this);
}

_isMounted = false;

componentDidMount() {
  this._isMounted = true;
  this.getStatusBosnia();
}

getStatusBosnia() {
  var url = `https://covid2019-api.herokuapp.com/v2/country/bosnia%20and%20herzegovina`;
  $.get(url, (data) => {
      //console.log(data);
      if (this._isMounted){
        this.setState({
          statusBosnia: data,
          loading: false
        });
      }
  });
}

componentWillUnmount() {
  this._isMounted = false;
}

  render(){
    if (this.state.loading === true) {
    return (
        <TableRow className="wrapper">
            <TableCell className="loading" colSpan="7">Učitavam podatke...</TableCell>
        </TableRow>
    )
    } else {
      console.log(this.state)
      let bosnia = this.state.statusBosnia;
      return (
          <TableRow>
              <TableCell align="right">3</TableCell>
              <TableCell align="right">Bosna i Hercegovina</TableCell>
              <TableCell align="right">3,511,372</TableCell>
              <TableCell align="right">{bosnia.data.confirmed}</TableCell>
              <TableCell align="right">{bosnia.data.active}</TableCell>
              <TableCell align="right">{bosnia.data.recovered}</TableCell>
              <TableCell align="right">{bosnia.data.deaths}</TableCell>
          </TableRow>
      );
    }
  }
}
export default BosniaComponent;