import React, { Component } from 'react';

import SerbiaComponent from './components/SerbiaComponent';
import CroatiaComponent from './components/CroatiaComponent';
import BosniaComponent from './components/BosniaComponent';
import MontenegroComponent from './components/MontenegroComponent';
import SloveniaComponent from './components/SloveniaComponent';
import MacedoniaComponent from './components/MacedoniaComponent';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';

class App extends Component {
  
  constructor(props) {
    super(props)
      this.state = {
        isActive:false
      }
  }
  
  handleShow = () => {
    if(this.state.isActive === false) {
      this.setState({
          isActive: true
      })
    } else {
      this.setState({
        isActive: false
      })
    }
  }

  render() {
  return (
    <div className="App">
      <div className="show">
        <Button variant="contained" onClick={this.handleShow}>{ !this.state.isActive ? "Prikaži" : "Sakri" } podatke</Button>
      </div>
      <div className="showResults">
        <h1>COVID 19 u zemljama bivse Juge:</h1>
        {this.state.isActive ? 
        <TableContainer component={Paper} id="prikazPodataka">
          <Table className="table" aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell align="right">RB</TableCell>
                <TableCell align="right">Zemlja</TableCell>
                <TableCell align="right">Ukupna populacija</TableCell>
                <TableCell align="right">Broj zaraženih</TableCell>
                <TableCell align="right">Broj aktivnih</TableCell>
                <TableCell align="right">Broj izlečenih</TableCell>
                <TableCell align="right">Broj preminulih</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
                <SerbiaComponent />
                <CroatiaComponent />
                <BosniaComponent />
                <MontenegroComponent />
                <SloveniaComponent />
                <MacedoniaComponent />
            </TableBody>
          </Table>
        </TableContainer>
        : null }
      </div>
    </div>
  );
  }
}

export default App;