import React, { Component } from 'react'
import * as $ from 'jquery';

import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';



class SerbiaComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
        statusSerbia: [],
        loading: true
    }
    this.getStatusCroatia = this.getStatusCroatia.bind(this);
}


_isMounted = false;

componentDidMount() {
  this._isMounted = true;
  this.getStatusCroatia();
}

getStatusCroatia() {
  var url = `https://covid2019-api.herokuapp.com/v2/country/croatia`;
  $.get(url, (data) => {
      //console.log(data);
      if (this._isMounted){
        this.setState({
          statusCroatia: data,
          loading: false
        });
      }
  });
}


componentWillUnmount() {
  this._isMounted = false;
}

  render(){
    if (this.state.loading === true) {
    return (
        <TableRow className="wrapper">
            <TableCell className="loading" colSpan="7">Učitavam podatke...</TableCell>
        </TableRow>
    )
} else {
    console.log(this.state)
    let croatia = this.state.statusCroatia;
    
    return (
    
        <TableRow>
            <TableCell align="right">2</TableCell>
            <TableCell align="right">Hrvatska</TableCell>
            <TableCell align="right">4,076,246</TableCell>
            <TableCell align="right">{croatia.data.confirmed}</TableCell>
            <TableCell align="right">{croatia.data.active}</TableCell>
            <TableCell align="right">{croatia.data.recovered}</TableCell>
            <TableCell align="right">{croatia.data.deaths}</TableCell>
        </TableRow>
               
  );
  }
}
}

export default SerbiaComponent;