import React, { Component } from 'react'
import * as $ from 'jquery';

import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';



class SerbiaComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
        statusSerbia: [],
        loading: true
    }
    this.getStatusSerbia = this.getStatusSerbia.bind(this);
}

_isMounted = false;

componentDidMount() {
  this._isMounted = true;
  this.getStatusSerbia();
}

getStatusSerbia() {
  var url = `https://covid2019-api.herokuapp.com/v2/country/serbia`;
  $.get(url, (data) => {
      //console.log(data);
      if (this._isMounted){
        this.setState({
          statusSerbia: data,
          loading: false
        });
      }
  });
}

componentWillUnmount() {
  this._isMounted = false;
}

  render(){
    if (this.state.loading === true) {
    return (
        <TableRow className="wrapper">
            <TableCell className="loading" colSpan="7">Učitavam podatke...</TableCell>
        </TableRow>
    )
} else {
    console.log(this.state)
    let serbia = this.state.statusSerbia;
    
    return (
    
        <TableRow>
            <TableCell align="right">1</TableCell>
            <TableCell align="right">Srbija</TableCell>
            <TableCell align="right">9,024,734 ( 7,186,862 )</TableCell>
            <TableCell align="right">{serbia.data.confirmed}</TableCell>
            <TableCell align="right">{serbia.data.active}</TableCell>
            <TableCell align="right">{serbia.data.recovered}</TableCell>
            <TableCell align="right">{serbia.data.deaths}</TableCell>
        </TableRow>
               
  );
  }
}
}

export default SerbiaComponent;