import React, { Component } from 'react'
import * as $ from 'jquery';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
class MacedoniaComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
        statusMacedonia: [],
        loading: true
    }
    this.getStatusMacedonia = this.getStatusMacedonia.bind(this);
}

_isMounted = false;

componentDidMount() {
  this._isMounted = true;
  this.getStatusMacedonia();
}

getStatusMacedonia() {
  var url = `https://covid2019-api.herokuapp.com/v2/country/north%20macedonia`;
  $.get(url, (data) => {
      //console.log(data);
      if (this._isMounted){
        this.setState({
          statusMacedonia: data,
          loading: false
        });
      }
  });
}

componentWillUnmount(){
  this._isMounted = false;
}

  render(){
    if (this.state.loading === true) {
    return (
        <TableRow className="wrapper">
            <TableCell className="loading" colSpan="7">Učitavam podatke...</TableCell>
        </TableRow>
    )
    } else {
      console.log(this.state)
      let macedonia = this.state.statusMacedonia;
      return (
          <TableRow>
              <TableCell align="right">6</TableCell>
              <TableCell align="right">Severna Makedonija</TableCell>
              <TableCell align="right">2,077,132</TableCell>
              <TableCell align="right">{macedonia.data.confirmed}</TableCell>
              <TableCell align="right">{macedonia.data.active}</TableCell>
              <TableCell align="right">{macedonia.data.recovered}</TableCell>
              <TableCell align="right">{macedonia.data.deaths}</TableCell>
          </TableRow>
      );
    }
  }
}
export default MacedoniaComponent;